<template>
  <div ref="chart" :id="id" :class="className"></div>
</template>

<script>
import { markRaw } from 'vue'
import * as echarts from 'echarts'

export default {
  data() {
    return {
      chart: null,
      data: [779, 199, 914],
      dataname: ['严重', '中等', '轻度'],
    }
  },
  props: {
    className: {
      type: String,
      default: 'chart'
    },
    id: {
      type: String,
      default: 'chart'
    },
  },
  watch: {
    data() {
      this.setOption();
    }
  },
  mounted() {
    this.initChart()
  },
  methods: {
    initChart() {
      this.chart = markRaw(echarts.init(this.$refs.chart));
      this.setOption();
      window.addEventListener('resize', this.resize);
    },
    setOption() {
      this.chart.setOption({
        legend: {
          top: 5,
          textStyle: {
            color: "#FFFFFF"
          }
        },
        xAxis: {
          type: 'value',
          max: this.data.reduce((acc, curr) => acc + curr, 0),
          splitLine: {
            show: false
          }
        },
        yAxis: {
          type: 'category',
          axisLine: {
            show: false,
          }
        },
        grid: {
          left: "0",
          top: "30",
          right: "0",
          bottom: "0"
        },
        series: [
          {
            name: this.dataname[0] + ' ( ' + this.data[0] + ' )',
            type: 'bar',
            stack: 'total',
            barWidth: '100%',
            // label: {
            //   show: true,
            //   color: "#FFFFFF"
            // },
            itemStyle: {
              color: "#ee6666"
            },
            data: [this.data[0]],
          },
          {
            name: this.dataname[1] + ' ( ' + this.data[1] + ' )',
            type: 'bar',
            stack: 'total',
            barWidth: '100%',
            // label: {
            //   show: true,
            //   color: "#FFFFFF"
            // },
            itemStyle: {
              color: "#fac858"
            },
            data: [this.data[1]],
          },
          {
            name: this.dataname[2] + ' ( ' + this.data[2] + ' )',
            type: 'bar',
            stack: 'total',
            barWidth: '100%',
            // label: {
            //   show: true,
            //   color: "#FFFFFF"
            // },
            itemStyle: {
              color: "#91cc75"
            },
            data: [this.data[2]],
          },
        ]
      });
    },
    resize() {
      this.chart.resize();
    },
  },
  beforeUnmount() {
    if (!this.chart) {
      return
    }
    window.removeEventListener('resize', this.resize)
    this.chart.dispose()
    this.chart = null
  },
}
</script>
