<template>
  <div ref="chart" :id="id" :class="className"></div>
</template>

<script>
import { markRaw } from 'vue'
import * as echarts from 'echarts'
import axios from 'axios'

export default {
  data() {
    return {
      chart: null,
      data: [
        {'name': '北京市', 'value': 866},
        {'name': '天津市', 'value': 257},
        {'name': '河北省', 'value': 779},
        {'name': '山西省', 'value': 199},
        {'name': '内蒙古自治区', 'value': 914},
        {'name': '辽宁省', 'value': 365},
        {'name': '吉林省', 'value': 107},
        {'name': '黑龙江省', 'value': 321},
        {'name': '上海市', 'value': 258},
        {'name': '江苏省', 'value': 900},
        {'name': '浙江省', 'value': 218},
        {'name': '安徽省', 'value': 150},
        {'name': '福建省', 'value': 967},
        {'name': '江西省', 'value': 46},
        {'name': '山东省', 'value': 488},
        {'name': '河南省', 'value': 122},
        {'name': '湖北省', 'value': 643},
        {'name': '湖南省', 'value': 241},
        {'name': '广东省', 'value': 977},
        {'name': '广西壮族自治区', 'value': 221},
        {'name': '海南省', 'value': 112},
        {'name': '重庆市', 'value': 633},
        {'name': '四川省', 'value': 854},
        {'name': '贵州省', 'value': 150},
        {'name': '云南省', 'value': 570},
        {'name': '西藏自治区', 'value': 273},
        {'name': '陕西省', 'value': 169},
        {'name': '甘肃省', 'value': 643},
        {'name': '青海省', 'value': 130},
        {'name': '宁夏回族自治区', 'value': 786},
        {'name': '新疆维吾尔自治区', 'value': 704},
        {'name': '台湾省', 'value': 485},
        {'name': '香港特别行政区', 'value': 524},
        {'name': '澳门特别行政区', 'value': 649},
      ]
    }
  },
  props: {
    className: {
      type: String,
      default: 'chart'
    },
    id: {
      type: String,
      default: 'chart'
    }
  },
  watch: {
    data() {
      this.setOption();
    }
  },
  mounted() {
    this.initChart()
  },
  beforeUnmount() {
    if (!this.chart) {
      return
    }
    window.removeEventListener('resize', this.resize)
    this.chart.dispose()
    this.chart = null
  },
  methods: {
    initChart() {
      this.chart = markRaw(echarts.init(this.$refs.chart))
      this.chart.showLoading();
      axios('/data/100000_full.json').then(response => {
        echarts.registerMap('China', response.data);
        this.chart.hideLoading();
        this.setOption();
        this.chart.on('click', (params) => {
          this.$emit('map_click', params.name);
        })
      });
      window.addEventListener('resize', this.resize)
    },
    setOption() {
      this.chart.setOption({
        select: {
          disabled: true,
        },
        tooltip: {
          formatter: '{b}<br/>用户数：{c}'
        },
        visualMap: {
          inRange: {
            color: [
              '#3DAAF7',
              '#0075bd',
            ]
          },
          show: false
        },
        series: [
          {
            type: 'map',
            map: 'China',
            zoom: 1.25,
            data: this.data,
            label: {
              color: "#FFFFFF"
            },
            emphasis: {
              label: {
                color: "#FFFFFF"
              }
            }
          }
        ]
      });
    },
    resize() {
      this.chart.resize();
    }
  }
}
</script>
