<template>
  <div ref="chart" :id="id" :class="className"></div>
</template>

<script>
import { markRaw } from 'vue'
import * as echarts from 'echarts'

export default {
  data() {
    return {
      chart: null,
      data: [
        [
          [79, 70, '杭州市'],
          [59, 44, '宁波市'],
          [90, 82, '温州市'],
          [20, 20, '嘉兴市'],
          [51, 1, '湖州市'],
          [91, 92, '绍兴市'],
          [16, 69, '金华市'],
          [7, 0, '衢州市'],
          [67, 71, '舟山市'],
          [53, 84, '台州市'],
          [64, 92, '丽水市']
        ],
        [
          [51, 83, '杭州市'],
          [40, 25, '宁波市'],
          [6, 28, '温州市'],
          [21, 44, '嘉兴市'],
          [40, 83, '湖州市'],
          [71, 33, '绍兴市'],
          [70, 33, '金华市'],
          [77, 67, '衢州市'],
          [18, 12, '舟山市'],
          [76, 28, '台州市'],
          [3, 26, '丽水市']
        ]
      ],
    }
  },
  props: {
    className: {
      type: String,
      default: 'chart'
    },
    id: {
      type: String,
      default: 'chart'
    },
  },
  watch: {
    data() {
      this.setOption();
    }
  },
  mounted() {
    this.initChart()
  },
  methods: {
    initChart() {
      this.chart = markRaw(echarts.init(this.$refs.chart));
      this.setOption();
      window.addEventListener('resize', this.resize);
    },
    setOption() {
      this.chart.setOption({
        legend: {
          data: ['男性', '女性'],
          right: 0,
          top: -5,
          textStyle: {
            color: "#FFFFFF"
          }
        },
        xAxis: {
          name: '存活率',
          min: 0,
          max: 100,
          nameLocation: 'middle',
          nameGap: 20,
          axisLine: {
            lineStyle: {
              color: "#FFFFFF"
            }
          },
          axisLabel: {
            formatter: '{value}%',
          },
          splitLine: {
            show: false,
          },
          axisTick: {
            show: false
          }
        },
        yAxis: {
          name: '人数',
          nameGap: 6,
          axisLine: {
            lineStyle: {
              color: "#FFFFFF"
            }
          },
          splitLine: {
            show: false,
          },
          axisTick: {
            show: false
          },
          axisLabel: {
            formatter: function(value) {
              if ((value / (100000000)) > 1) {
                return Math.floor(value / 100000000) + " 亿";
              } else if ((value / (10000000)) > 1) {
                return Math.floor(value / 10000000) + " 千万";
              } else if ((value / (1000000)) > 1) {
                return Math.floor(value / 1000000) + " 百万";
              } else if ((value / (100000)) > 1) {
                return Math.floor(value / 100000) + " 十万";
              } else if ((value / (10000)) > 1) {
                return Math.floor(value / 10000) + " 万";
              } else if ((value / (1000)) > 1) {
                return Math.floor(value / 1000) + " 千";
              } else {
                return value;
              }
            }
          }
        },
        grid: {
          left: "40",
          top: "20",
          right: "20",
          bottom: "30"
        },
        series: [
          {
            name: '男性',
            data: this.data[0],
            type: 'scatter',
            symbolSize: function (data) {
              return Math.pow(Math.sqrt(data[1] + 5), 1.1);
            },
            emphasis: {
              focus: 'series',
              label: {
                show: true,
                formatter: function (param) {
                  return param.data[2];
                },
                position: 'top'
              }
            },
            itemStyle: {
              color: new echarts.graphic.RadialGradient(0.4, 0.3, 1, [
                {
                  offset: 0,
                  color: 'rgb(129, 227, 238)'
                },
                {
                  offset: 1,
                  color: 'rgb(25, 183, 207)'
                }
              ])
            }
          },
          {
            name: '女性',
            data: this.data[1],
            type: 'scatter',
            symbolSize: function (data) {
              return Math.pow(Math.sqrt(data[1] + 5), 1.1);
            },
            emphasis: {
              focus: 'series',
              label: {
                show: true,
                formatter: function (param) {
                  return param.data[2];
                },
                position: 'top'
              }
            },
            itemStyle: {
              color: new echarts.graphic.RadialGradient(0.4, 0.3, 1, [
                {
                  offset: 0,
                  color: 'rgb(251, 118, 123)'
                },
                {
                  offset: 1,
                  color: 'rgb(204, 46, 72)'
                }
              ])
            }
          }
        ]
      });
    },
    resize() {
      this.chart.resize();
    }
  },
  beforeUnmount() {
    if (!this.chart) {
      return
    }
    window.removeEventListener('resize', this.resize)
    this.chart.dispose()
    this.chart = null
  },
}
</script>
