<template>
  <div ref="chart" :id="id" :class="className"></div>
</template>

<script>
import { markRaw } from 'vue'
import * as echarts from 'echarts'

export default {
  data() {
    return {
      chart: null,
      x: ['0-18', '19-40', '41-60', '61-70', '71-75', '76-80', '81-85', '86-90', '91-100', '100+'],
      data: [[3, 9, 34, 42, 67, 78, 91, 98, 87, 55], [5, 10, 33, 45, 78, 88, 95, 76, 67, 51]],
      dataname: ['男性', '女性'],
    }
  },
  props: {
    className: {
      type: String,
      default: 'chart'
    },
    id: {
      type: String,
      default: 'chart'
    },
  },
  watch: {
    data() {
      this.setOption();
    }
  },
  mounted() {
    this.initChart()
  },
  methods: {
    initChart() {
      this.chart = markRaw(echarts.init(this.$refs.chart));
      this.setOption();
      window.addEventListener('resize', this.resize);
    },
    setOption() {
      this.chart.setOption({
        legend: {
          top: -5,
          right: 0,
          textStyle: {
            color: "#FFFFFF"
          }
        },
        xAxis: {
          type: 'category',
          data: this.x,
          axisLabel: {
            interval: 0
          },
          axisTick: {
            show: false
          },
          axisLine: {
            lineStyle: {
              color: "#FFFFFF"
            }
          }
        },
        yAxis: {
          type: 'value',
          splitLine: {
            // show: false,
            lineStyle: {
              color: "#3DAAF766"
            }
          },
          axisLine: {
            show: true,
            lineStyle: {
              color: "#FFFFFF"
            }
          },
          axisLabel: {
            formatter: function(value) {
              if ((value / (100000000)) > 1) {
                return Math.floor(value / 100000000) + " 亿";
              } else if ((value / (10000000)) > 1) {
                return Math.floor(value / 10000000) + " 千万";
              } else if ((value / (1000000)) > 1) {
                return Math.floor(value / 1000000) + " 百万";
              } else if ((value / (100000)) > 1) {
                return Math.floor(value / 100000) + " 十万";
              } else if ((value / (10000)) > 1) {
                return Math.floor(value / 10000) + " 万";
              } else if ((value / (1000)) > 1) {
                return Math.floor(value / 1000) + " 千";
              } else {
                return value;
              }
            }
          }
        },
        grid: {
          left: "40",
          top: "35",
          right: "0",
          bottom: "20"
        },
        series: [
          {
            name: this.dataname[0] + ' ( ' + this.data[0].reduce((acc, curr) => acc + curr, 0) + ' )',
            type: 'bar',
            label: {
              show: true,
              color: "#FFFFFF",
              position: "top"
            },
            itemStyle: {
              color: new echarts.graphic.LinearGradient(0, 1, 0, 0, [
                {
                  offset: 0,
                  color: '#0075BD'
                },
                {
                  offset: 1,
                  color: '#00FFFF'
                },
              ]),
            },
            data: this.data[0],
          },
          {
            name: this.dataname[1] + ' ( ' + this.data[1].reduce((acc, curr) => acc + curr, 0) + ' )',
            type: 'bar',
            // barGap: '20%',
            barCategoryGap: "15%",
            label: {
              show: true,
              color: "#FFFFFF",
              position: "top"
            },
            itemStyle: {
              color: new echarts.graphic.LinearGradient(0, 1, 0, 0, [
                {
                  offset: 0,
                  color: '#956900'
                },
                {
                  offset: 1,
                  color: '#FFD500'
                },
              ]),
            },
            data: this.data[1],
          },
        ]
      });
    },
    resize() {
      this.chart.resize();
    },
  },
  beforeUnmount() {
    if (!this.chart) {
      return
    }
    window.removeEventListener('resize', this.resize)
    this.chart.dispose()
    this.chart = null
  },
}
</script>
