<template>
  <div ref="chart" :id="id" :class="className"></div>
</template>

<script>
import { markRaw } from 'vue'
import * as echarts from 'echarts'

export default {
  data() {
    return {
      chart: null,
      data: [
        ['北京市', '天津市', '河北省', '山西省', '内蒙古自治区', '辽宁省', '吉林省', '黑龙江省', '上海市', '江苏省', '浙江省', '安徽省', '福建省', '江西省', '山东省', '河南省', '湖北省', '湖南省', '广东省', '广西壮族自治区', '海南省', '重庆市', '四川省', '贵州省', '云南省', '西藏自治区', '陕西省', '甘肃省', '青海省', '宁夏回族自治区', '新疆维吾尔自治区', '台湾省', '香港特别行政区', '澳门特别行政区'],
        [866, 257, 779, 199, 914, 365, 107, 321, 258, 900, 218, 150, 967, 46, 488, 122, 643, 241, 977, 221, 112, 633, 854, 150, 570, 273, 169, 643, 130, 786, 704, 485, 524, 649],
        [972, 638, 408, 41, 268, 420, 540, 12, 188, 949, 987, 462, 657, 193, 248, 873, 75, 150, 948, 396, 481, 525, 648, 133, 831, 461, 263, 64, 157, 962, 923, 218, 501, 352]
      ],
      dataname: ['本月', '上月'],
      timer: null
    }
  },
  props: {
    className: {
      type: String,
      default: 'chart'
    },
    id: {
      type: String,
      default: 'chart'
    },
  },
  watch: {
    data() {
      this.setOption();
    }
  },
  mounted() {
    this.initChart()
  },
  methods: {
    initChart() {
      // this.endValue = this.startValue + this.count - 1
      this.chart = markRaw(echarts.init(this.$refs.chart));
      this.setOption();
      window.addEventListener('resize', this.resize);
      this.timer = setInterval(this.valChange, 2500);
    },
    setOption() {
      this.chart.setOption({
        legend: {
          right: 0,
          top: -5,
          textStyle: {
            color: "#FFFFFF"
          }
        },
        xAxis: {
          type: 'value',
          max: Math.max(Math.max(...this.data[1]), Math.max(...this.data[2])) * 1.15,
          splitLine: {
            show: false
          },
        },
        yAxis: {
          type: 'category',
          data: this.data[0],
          max: 6,
          inverse: true,
          axisLabel: {
            color: "#FFFFFF",
          },
          axisTick: {
            length: 120,
            lineStyle: {
              color: "#3DAAF766"
            }
          },
          splitLine: {
            show: true,
            lineStyle: {
              color: "#3DAAF766"
            }
          },
          axisLine: {
            show: false
          }
        },
        grid: {
          left: "120",
          top: "17",
          right: "0",
          bottom: "0"
        },
        series: [
          {
            name: this.dataname[0] + ' ( ' + this.data[1].reduce((acc, curr) => acc + curr, 0) + ' )',
            data: this.data[1],
            type: 'bar',
            label: {
              show: true,
              color: "#FFFFFF",
              position: "right"
            },
            itemStyle: {
              color: new echarts.graphic.LinearGradient(1, 0, 0, 0, [
                {
                  offset: 0,
                  color: '#0075BD'
                },
                {
                  offset: 1,
                  color: '#00FFFF'
                },
              ]),
            },
            barWidth: "35%",
          },
          {
            name: this.dataname[1] + ' ( ' + this.data[2].reduce((acc, curr) => acc + curr, 0) + ' )',
            type: 'bar',
            data: this.data[2],
            label: {
              show: true,
              color: "#FFFFFF",
              position: "right"
            },
            itemStyle: {
              color: new echarts.graphic.LinearGradient(1, 0, 0, 0, [
                {
                  offset: 0,
                  color: '#956900'
                },
                {
                  offset: 1,
                  color: '#FFD500'
                },
              ]),
            },
            barWidth: "35%",
          },
        ]
      });
    },
    resize() {
      this.chart.resize();
    },
    valChange() {
      var data_name_temp = this.data[0].shift();
      this.data[0].push(data_name_temp);
      var data_now_temp = this.data[1].shift();
      this.data[1].push(data_now_temp);
      var data_last_temp = this.data[2].shift();
      this.data[2].push(data_last_temp);
      this.setOption();
    }
  },
  beforeUnmount() {
    if (!this.chart) {
      return
    }
    window.removeEventListener('resize', this.resize)
    clearInterval(this.timer);
    this.chart.dispose()
    this.chart = null
  },
}
</script>
