<template>
  <div ref="chart" :id="id" :class="className"></div>
</template>

<script>
import { markRaw } from 'vue'
import * as echarts from 'echarts'

export default {
  data() {
    return {
      chart: null,
      data: [
        [],
        []
      ],
      dataname: ['成本', '毛利润'],
    }
  },
  props: {
    className: {
      type: String,
      default: 'chart'
    },
    id: {
      type: String,
      default: 'chart'
    },
  },
  watch: {
    data() {
      this.setOption();
    }
  },
  mounted() {
    let base = +new Date(2022, 0, 1);
    let today = +new Date(); // 获取当前日期的时间戳
    let oneDay = 24 * 3600 * 1000;
    this.data[0] = [[base, Math.random()]];
    this.data[1] = [[base, Math.random()]];
    while (base < today) {
      let now = new Date((base += oneDay));
      this.data[0].push([+now, Math.round(Math.random() * 200)]);
      this.data[1].push([+now, Math.round(Math.random() * 200)]);
    }
    this.initChart()
  },
  methods: {
    initChart() {
      this.chart = markRaw(echarts.init(this.$refs.chart));
      this.setOption();
      window.addEventListener('resize', this.resize);
    },
    setOption() {
      this.chart.setOption({
        xAxis: {
          type: 'time',
          minInterval: 24 * 3600 * 1000,
          axisLine: {
            lineStyle: {
              color: "#FFFFFF"
            }
          }
        },
        yAxis: {
          type: 'value',
          splitLine: {
            lineStyle: {
              color: "#3DAAF766"
            }
          },
          axisLine: {
            show: true,
            lineStyle: {
              color: "#FFFFFF"
            }
          },
          axisLabel: {
            formatter: function(value) {
              if ((value / (100000000)) > 1) {
                return Math.floor(value / 100000000) + " 亿";
              } else if ((value / (10000000)) > 1) {
                return Math.floor(value / 10000000) + " 千万";
              } else if ((value / (1000000)) > 1) {
                return Math.floor(value / 1000000) + " 百万";
              } else if ((value / (100000)) > 1) {
                return Math.floor(value / 100000) + " 十万";
              } else if ((value / (10000)) > 1) {
                return Math.floor(value / 10000) + " 万";
              } else if ((value / (1000)) > 1) {
                return Math.floor(value / 1000) + " 千";
              } else {
                return value;
              }
            }
          }
        },
        dataZoom: [
          {
            type: 'inside',
            startValue: Math.max(
              this.data[0].reduce((max, currentValue) => currentValue[0] > max ? currentValue[0] : max, this.data[0][0][0]),
              this.data[1].reduce((max, currentValue) => currentValue[0] > max ? currentValue[0] : max, this.data[1][0][0]),
            ) - 24 * 3600 * 1000 * 31,
          },
          {}
        ],
        grid: {
          left: "40",
          top: "10",
          right: "10",
          bottom: "70"
        },
        series: [
          {
            name: this.dataname[0],
            type: 'line',
            data: this.data[0],
            smooth: true,
            symbol: 'none',
            itemStyle: {
              color: "#2676d0"
            },
          },
          {
            name: this.dataname[1],
            type: 'line',
            data: this.data[1],
            smooth: true,
            symbol: 'none',
            itemStyle: {
              color: "#FFd500"
            },
          },
        ]
      });
    },
    resize() {
      this.chart.resize();
    },
  },
  beforeUnmount() {
    if (!this.chart) {
      return
    }
    window.removeEventListener('resize', this.resize)
    this.chart.dispose()
    this.chart = null
  },
}
</script>
