<template>
  <div ref="html_table" :id="id" :class="className">
    <table>
      <tr v-for="row in html.data" :key="row">
        <td class="rank"><p>{{ row.rank }}</p></td>
        <td class="name">{{ row.name }}</td>
        <td class="income">{{ row.income }}</td>
        <td class="rank_change" :class="row.rank_change_state">{{ row.rank_change_state + row.rank_change }}</td>
      </tr>
    </table>
  </div>
</template>

<script>
// import { markRaw } from 'vue'

export default {
  data() {
    return {
      html: {
        data:[],
        dom: null
      },
      data: [
        ['自己', 159785, 89498],
        ['华为', 156189, 176168],
        ['小米', 123597, 168944],
        ['安防', 180658, 75649],
        ['其他', 225958, 189419],
      ],
    }
  },
  props: {
    className: {
      type: String,
      default: 'html html_table'
    },
    id: {
      type: String,
      default: 'html_table'
    },
  },
  watch: {
    data() {
      this.setData();
    }
  },
  mounted() {
    this.init()
    this.setData()
  },
  methods: {
    init() {
      this.html.dom = this.$refs.html_table
    },
    setData(){
      // 按第二个字段进行降序排序
      let sortedDataByField2 = this.data.slice().sort(function(a, b) {
        return b[1] - a[1];
      });

      // 按第三个字段进行降序排序
      let sortedDataByField3 = this.data.slice().sort(function(a, b) {
        return b[2] - a[2];
      });

      // 获取排名名次
      let data = sortedDataByField2.map(function(item, index) {
        var rankField2 = index + 1;
        var rankField3 = sortedDataByField3.findIndex(function(element){
          return element[0] === item[0];
        }) + 1;
        return {
          name: item[0],
          now: item[1],
          last: item[2],
          now_rank: rankField2,
          last_rank: rankField3
        };
      });

      this.html.data.length = 0
      for (let i = 0; i < 5 && i < data.length; i++) {
          let item = data[i];
          let rank_change = item.last_rank - item.now_rank
          this.html.data.push({
            rank: 'No.' + item.now_rank,
            name: item.name,
            income: item.now,
            rank_change: rank_change == 0 ? "-" : (rank_change>0 ? rank_change : -rank_change),
            rank_change_state: rank_change == 0 ? "-" : (rank_change>0 ? "↑" : "↓")
          });
      }

      // 输出排序后的结果
      console.log(data);
    }
  },
  beforeUnmount() {

  },
}
</script>

<style>
.html_table table {
  color: #FFFFFF;
  width: 100%;
  height: 100%;
  border-collapse: collapse;
}
.html_table td {
  border-bottom: 1px solid #3DAAF766; /* 设置单元格下边框 */
}
.html_table .rank {
  width: 6rem;
  text-align: center;
}
.html_table .rank p {
  display: inline-block;
  padding-left: 1px;
  padding-right: 1px;
}
.html_table tr:nth-child(-n+3) .rank p {
  border: 1px solid #FFd500;
  font-size: 0.75rem;
}
.html_table tr:nth-child(n+4) .rank p {
  font-size: 0.8rem;
}
.html_table .name {
  width: 8rem;
  text-align: center;
}
.html_table .income {
  text-align: center;
}
.html_table .rank_change {
  width: 5rem;
  text-align: center;
}
.html_table .rank_change.\- {
  color: #909399;
}
.html_table .rank_change.↑ {
  color: #67C23A;
}
.html_table .rank_change.↓ {
  color: #F56C6C;
}

</style>