<template>
  <div ref="chart" :id="id" :class="className"></div>
</template>

<script>
import { markRaw } from 'vue'
import * as echarts from 'echarts'

export default {
  data() {
    return {
      chart: null,
      data: [
        {'name': '线上', 'value': 258},
        {'name': '线下', 'value': 150},
      ]
    }
  },
  props: {
    className: {
      type: String,
      default: 'chart'
    },
    id: {
      type: String,
      default: 'chart'
    },
  },
  watch: {
    data() {
      this.setOption();
    }
  },
  mounted() {
    this.initChart()
  },
  beforeUnmount() {
    if (!this.chart) {
      return
    }
    window.removeEventListener('resize', this.resize)
    this.chart.dispose()
    this.chart = null
  },
  methods: {
    initChart() {
      this.chart = markRaw(echarts.init(this.$refs.chart));
      this.setOption();
      window.addEventListener('resize', this.resize);
    },
    setOption() {
      this.chart.setOption({
        series: [
          {
            data: this.data,
            type: 'pie',
            radius: '90%',
            color: ["#3EDFF6", "#FFD500"],
            label: {
              position: "inside",
              color: "#FFF",
              textBorderColor: "#00000044",
              textBorderWidth: 3,
              fontSize: 17,
              formatter: "{d}%\r\n{b}",
            },
          }
        ]
      });
    },
    resize() {
      this.chart.resize();
    }
  }
}
</script>
