<template>
  <div ref="chart" :id="id" :class="className"></div>
</template>

<script>
import { markRaw } from 'vue'
import * as echarts from 'echarts'
import axios from 'axios'

export default {
  data() {
    return {
      chart: null,
      data: [
        {'name': '杭州市', 'value': 153},
        {'name': '宁波市', 'value': 69},
        {'name': '温州市', 'value': 110},
        {'name': '嘉兴市', 'value': 64},
        {'name': '湖州市', 'value': 84},
        {'name': '绍兴市', 'value': 125},
        {'name': '金华市', 'value': 102},
        {'name': '衢州市', 'value': 67},
        {'name': '舟山市', 'value': 83},
        {'name': '台州市', 'value': 112},
        {'name': '丽水市', 'value': 118}
      ],
      files: {
        '北京市': '110000_full',
        '天津市': '120000_full',
        '河北省': '130000_full',
        '山西省': '140000_full',
        '内蒙古自治区': '150000_full',
        '辽宁省': '210000_full',
        '吉林省': '220000_full',
        '黑龙江省': '230000_full',
        '上海市': '310000_full',
        '江苏省': '320000_full',
        '浙江省': '330000_full',
        '安徽省': '340000_full',
        '福建省': '350000_full',
        '江西省': '360000_full',
        '山东省': '370000_full',
        '河南省': '410000_full',
        '湖北省': '420000_full',
        '湖南省': '430000_full',
        '广东省': '440000_full',
        '广西壮族自治区': '450000_full',
        '海南省': '460000_full',
        '重庆市': '500000_full',
        '四川省': '510000_full',
        '贵州省': '520000_full',
        '云南省': '530000_full',
        '西藏自治区': '540000_full',
        '陕西省': '610000_full',
        '甘肃省': '620000_full',
        '青海省': '630000_full',
        '宁夏回族自治区': '640000_full',
        '新疆维吾尔自治区': '650000_full',
        '台湾省': '710000',
        '香港特别行政区': '810000_full',
        '澳门特别行政区': '820000_full'
      }
    }
  },
  props: {
    className: {
      type: String,
      default: 'chart'
    },
    id: {
      type: String,
      default: 'chart'
    },
    province: {
      type: String,
      default: '浙江省'
    }
  },
  watch: {
    province(newVal) {
      this.readMap(newVal)
    },
    data() {
      this.setOption();
    }
  },
  mounted() {
    this.initChart()
  },
  beforeUnmount() {
    if (!this.chart) {
      return
    }
    window.removeEventListener('resize', this.resize)
    this.chart.dispose()
    this.chart = null
  },
  methods: {
    initChart() {
      this.chart = markRaw(echarts.init(this.$refs.chart))
      this.readMap(this.province)
      window.addEventListener('resize', this.resize)
    },
    readMap(filename) {
      this.chart.showLoading();
      // axios('').then(response => {
      //   this.data = response.data;
      // });
      axios('/data/'+this.files[filename]+'.json').then(response => {
        echarts.registerMap('Province', response.data);
        this.setOption();
        this.chart.hideLoading();
      });
    },
    setOption() {
      this.chart.setOption({
        select: {
          disabled: true,
        },
        tooltip: {
          formatter: '{b}<br/>用户数：{c}'
        },
        visualMap: {
          inRange: {
            color: [
              '#3DAAF7',
              '#0075bd',
            ]
          },
          show: false
        },
        series: [
          {
            type: 'map',
            map: 'Province',
            zoom: 1.25,
            data: this.data,
            label: {
              color: "#FFFFFF"
            },
            emphasis: {
              label: {
                color: "#FFFFFF"
              }
            }
          }
        ]
      });
    },
    resize() {
      this.chart.resize();
    }
  }
}
</script>
