<template>
  <div ref="chart" :id="id" :class="className"></div>
</template>

<script>
import { markRaw } from 'vue'
import * as echarts from 'echarts'

export default {
  data() {
    return {
      chart: null,
      data: [76928, 23456, 86542, 97431, 78912, 54321],
      x: ['2022', '2023', '2024', '2025', '2026', '2027'],
    }
  },
  props: {
    className: {
      type: String,
      default: 'chart'
    },
    id: {
      type: String,
      default: 'chart'
    },
  },
  watch: {
    data() {
      this.setOption();
    }
  },
  mounted() {
    this.initChart()
  },
  methods: {
    initChart() {
      this.chart = markRaw(echarts.init(this.$refs.chart));
      this.setOption();
      window.addEventListener('resize', this.resize);
    },
    setOption() {
      this.chart.setOption({
        tooltip: {
          trigger: 'axis'
        },
        xAxis: {
          type: 'category',
          data: this.x,
          axisLine: {
            lineStyle: {
              color: "#FFFFFF"
            }
          }
        },
        yAxis: {
          type: 'value',
          splitLine: {
            lineStyle: {
              color: "#3DAAF766"
            }
          },
          axisLine: {
            show: true,
            lineStyle: {
              color: "#FFFFFF"
            }
          },
          axisLabel: {
            formatter: function(value) {
              if ((value / (100000000)) > 1) {
                return Math.floor(value / 100000000) + " 亿";
              } else if ((value / (10000000)) > 1) {
                return Math.floor(value / 10000000) + " 千万";
              } else if ((value / (1000000)) > 1) {
                return Math.floor(value / 1000000) + " 百万";
              } else if ((value / (100000)) > 1) {
                return Math.floor(value / 100000) + " 十万";
              } else if ((value / (10000)) > 1) {
                return Math.floor(value / 10000) + " 万";
              } else if ((value / (1000)) > 1) {
                return Math.floor(value / 1000) + " 千";
              } else {
                return value;
              }
            }
          }
        },
        grid: {
          left: "40",
          top: "20",
          right: "0",
          bottom: "20"
        },
        series: {
          type: 'line',
          data: this.data,
          smooth: true,
          itemStyle: {
            color: "#2676d0"
          },
          label: {
            show: true,
            color: "#FFFFFF",
            position: "inside",
            formatter:(params) => {
              let left = params.dataIndex > 0 ? this.data[params.dataIndex-1] - params.value : 0;
              let right = params.dataIndex < this.data.length-1 ? this.data[params.dataIndex+1] - params.value : 0;
              let max = Math.abs(left) > Math.abs(right) ? left : right;
              return max < 0 ? params.data + '\n\n\n' : '\n\n\n' + params.value;
            },
          }
        },
      });
    },
    resize() {
      this.chart.resize();
    },
  },
  beforeUnmount() {
    if (!this.chart) {
      return
    }
    window.removeEventListener('resize', this.resize)
    this.chart.dispose()
    this.chart = null
  },
}
</script>
